/**
 * Component Style Manager
 * Provides scoped CSS system for component isolation and better maintainability
 */

export interface StyleDefinition {
  selector: string;
  properties: Record<string, string | number>;
}

export interface ComponentStyles {
  componentId: string;
  styles: StyleDefinition[];
  globals?: StyleDefinition[];
  variables?: Record<string, string>;
}

export interface StylesOptions {
  scoped?: boolean;
  namespace?: string;
  minify?: boolean;
  autoprefixer?: boolean;
}

/**
 * CSS-in-JS Style Manager for Components
 */
export class ComponentStyleManager {
  private static instance: ComponentStyleManager;
  private styleSheets = new Map<string, HTMLStyleElement>();
  private scopedStyles = new Map<string, ComponentStyles>();
  private cssVariables = new Map<string, Record<string, string>>();
  
  private constructor() {}
  
  static getInstance(): ComponentStyleManager {
    if (!ComponentStyleManager.instance) {
      ComponentStyleManager.instance = new ComponentStyleManager();
    }
    return ComponentStyleManager.instance;
  }

  /**
   * Register component styles
   */
  registerComponentStyles(
    componentId: string,
    styles: ComponentStyles,
    options: StylesOptions = {}
  ): string {
    const {
      scoped = true,
      namespace = componentId,
      minify = false
    } = options;

    // Generate unique scope identifier
    const scopeId = this.generateScopeId(componentId);
    
    // Process styles with scoping
    const processedCSS = this.processStyles(styles, scopeId, scoped, namespace);
    
    // Create or update style element
    this.createStyleElement(componentId, processedCSS, minify);
    
    // Store styles for hot reloading
    this.scopedStyles.set(componentId, styles);
    
    // Store CSS variables
    if (styles.variables) {
      this.cssVariables.set(componentId, styles.variables);
      this.applyVariables(componentId, styles.variables);
    }
    
    return scopeId;
  }

  /**
   * Create styles using template literals (CSS-in-JS syntax)
   */
  css(
    template: TemplateStringsArray,
    ...values: (string | number)[]
  ): string {
    let result = template[0] || '';
    
    for (let i = 0; i < values.length; i++) {
      result += String(values[i]) + (template[i + 1] || '');
    }
    
    return result;
  }

  /**
   * Create styled component with automatic scoping
   */
  styled(
    componentId: string,
    styles: string,
    options: StylesOptions = {}
  ): {
    scopeId: string;
    className: (name: string) => string;
  } {
    const scopeId = this.generateScopeId(componentId);
    
    // Parse CSS string into style definitions
    const parsedStyles = this.parseCSS(styles);
    
    const componentStyles: ComponentStyles = {
      componentId,
      styles: parsedStyles
    };
    
    this.registerComponentStyles(componentId, componentStyles, options);
    
    return {
      scopeId,
      className: (name: string) => this.getScopedClassName(name, scopeId)
    };
  }

  /**
   * Generate scoped class name
   */
  getScopedClassName(className: string, scopeId: string): string {
    return `${className}_${scopeId}`;
  }

  /**
   * Apply theme variables to a component
   */
  applyTheme(componentId: string, themeVariables: Record<string, string>): void {
    const existingVariables = this.cssVariables.get(componentId) || {};
    const mergedVariables = { ...existingVariables, ...themeVariables };
    
    this.cssVariables.set(componentId, mergedVariables);
    this.applyVariables(componentId, mergedVariables);
  }

  /**
   * Remove component styles
   */
  removeComponentStyles(componentId: string): void {
    const styleElement = this.styleSheets.get(componentId);
    if (styleElement && styleElement.parentNode) {
      styleElement.parentNode.removeChild(styleElement);
    }
    
    this.styleSheets.delete(componentId);
    this.scopedStyles.delete(componentId);
    this.cssVariables.delete(componentId);
  }

  /**
   * Hot reload component styles
   */
  hotReloadStyles(componentId: string, newStyles: ComponentStyles): void {
    const scopeId = this.generateScopeId(componentId);
    const processedCSS = this.processStyles(newStyles, scopeId, true, componentId);
    
    const styleElement = this.styleSheets.get(componentId);
    if (styleElement) {
      styleElement.textContent = processedCSS;
    }
    
    this.scopedStyles.set(componentId, newStyles);
  }

  /**
   * Get all registered component styles
   */
  getAllStyles(): Map<string, ComponentStyles> {
    return new Map(this.scopedStyles);
  }

  /**
   * Generate unique scope identifier
   */
  private generateScopeId(componentId: string): string {
    // Generate hash-like identifier for scoping
    let hash = 0;
    for (let i = 0; i < componentId.length; i++) {
      const char = componentId.charCodeAt(i);
      hash = ((hash << 5) - hash) + char;
      hash = hash & hash; // Convert to 32-bit integer
    }
    return Math.abs(hash).toString(36).substring(0, 6);
  }

  /**
   * Process styles with scoping and namespacing
   */
  private processStyles(
    styles: ComponentStyles,
    scopeId: string,
    scoped: boolean,
    namespace: string
  ): string {
    let css = '';
    
    // Add CSS variables
    if (styles.variables) {
      css += this.generateVariablesCSS(styles.variables, scopeId);
    }
    
    // Process component styles
    for (const style of styles.styles) {
      const selector = scoped 
        ? this.scopeSelector(style.selector, scopeId)
        : style.selector;
      
      css += this.generateRule(selector, style.properties);
    }
    
    // Process global styles (not scoped)
    if (styles.globals) {
      for (const globalStyle of styles.globals) {
        css += this.generateRule(globalStyle.selector, globalStyle.properties);
      }
    }
    
    return css;
  }

  /**
   * Scope CSS selector
   */
  private scopeSelector(selector: string, scopeId: string): string {
    // Handle various selector types
    if (selector.startsWith('@')) {
      // Media queries, keyframes, etc.
      return selector;
    }
    
    if (selector.includes(',')) {
      // Multiple selectors
      return selector
        .split(',')
        .map(s => this.scopeSingleSelector(s.trim(), scopeId))
        .join(', ');
    }
    
    return this.scopeSingleSelector(selector, scopeId);
  }

  /**
   * Scope a single selector
   */
  private scopeSingleSelector(selector: string, scopeId: string): string {
    // Component root selector
    if (selector === ':host' || selector === '&') {
      return `[data-component-scope="${scopeId}"]`;
    }
    
    // Child selectors
    if (selector.startsWith('.') || selector.startsWith('#') || /^[a-zA-Z]/.test(selector)) {
      return `[data-component-scope="${scopeId}"] ${selector}`;
    }
    
    // Pseudo selectors
    if (selector.startsWith(':')) {
      return `[data-component-scope="${scopeId}"]${selector}`;
    }
    
    return selector;
  }

  /**
   * Generate CSS rule
   */
  private generateRule(selector: string, properties: Record<string, string | number>): string {
    const props = Object.entries(properties)
      .map(([key, value]) => `  ${this.kebabCase(key)}: ${value};`)
      .join('\n');
    
    return `${selector} {\n${props}\n}\n\n`;
  }

  /**
   * Generate CSS variables rule
   */
  private generateVariablesCSS(variables: Record<string, string>, scopeId: string): string {
    const props = Object.entries(variables)
      .map(([key, value]) => `  --${key}: ${value};`)
      .join('\n');
    
    return `[data-component-scope="${scopeId}"] {\n${props}\n}\n\n`;
  }

  /**
   * Create or update style element
   */
  private createStyleElement(componentId: string, css: string, minify: boolean): void {
    let styleElement = this.styleSheets.get(componentId);
    
    if (!styleElement) {
      styleElement = document.createElement('style');
      styleElement.setAttribute('data-component-styles', componentId);
      document.head.appendChild(styleElement);
      this.styleSheets.set(componentId, styleElement);
    }
    
    styleElement.textContent = minify ? this.minifyCSS(css) : css;
  }

  /**
   * Apply CSS variables to component
   */
  private applyVariables(componentId: string, variables: Record<string, string>): void {
    // Variables are already included in the CSS, but we can also apply them dynamically
    const elements = document.querySelectorAll(`[data-component-scope="${this.generateScopeId(componentId)}"]`);
    
    elements.forEach(element => {
      const htmlElement = element as HTMLElement;
      Object.entries(variables).forEach(([key, value]) => {
        htmlElement.style.setProperty(`--${key}`, value);
      });
    });
  }

  /**
   * Parse CSS string into style definitions
   */
  private parseCSS(css: string): StyleDefinition[] {
    const styles: StyleDefinition[] = [];
    
    // Simple CSS parser (for basic CSS-in-JS support)
    const rules = css.match(/([^{}]+)\{([^{}]+)\}/g) || [];
    
    rules.forEach(rule => {
      const [, selector, properties] = rule.match(/([^{}]+)\{([^{}]+)\}/) || [];
      if (selector && properties) {
        const props: Record<string, string> = {};
        
        properties.split(';').forEach(prop => {
          const [key, value] = prop.split(':').map(s => s.trim());
          if (key && value) {
            props[this.camelCase(key)] = value;
          }
        });
        
        styles.push({
          selector: selector.trim(),
          properties: props
        });
      }
    });
    
    return styles;
  }

  /**
   * Convert camelCase to kebab-case
   */
  private kebabCase(str: string): string {
    return str.replace(/[A-Z]/g, match => `-${match.toLowerCase()}`);
  }

  /**
   * Convert kebab-case to camelCase
   */
  private camelCase(str: string): string {
    return str.replace(/-([a-z])/g, (_, letter) => letter.toUpperCase());
  }

  /**
   * Minify CSS (basic implementation)
   */
  private minifyCSS(css: string): string {
    return css
      .replace(/\s+/g, ' ')
      .replace(/;\s*}/g, '}')
      .replace(/\s*{\s*/g, '{')
      .replace(/\s*}\s*/g, '}')
      .replace(/\s*,\s*/g, ',')
      .replace(/\s*;\s*/g, ';')
      .trim();
  }
}

// Export singleton instance
export const styleManager = ComponentStyleManager.getInstance();

/**
 * Utility function for creating component styles
 */
export function createComponentStyles(
  componentId: string,
  stylesFn: () => ComponentStyles,
  options?: StylesOptions
): string {
  const styles = stylesFn();
  return styleManager.registerComponentStyles(componentId, styles, options);
}

/**
 * CSS template literal function
 */
export function css(template: TemplateStringsArray, ...values: (string | number)[]): string {
  return styleManager.css(template, ...values);
}