/**
 * Enhanced Component Base Class
 * Provides standardized lifecycle, error handling, and performance monitoring
 */

import type { IComponent } from '../types/index.js';
import { eventBus } from '../events/event-bus.js';

export interface ComponentState {
  isMounted: boolean;
  isDestroyed: boolean;
  hasError: boolean;
  errorMessage?: string;
  mountTime?: number;
  updateCount: number;
}

export interface ComponentPerformanceMetrics {
  mountTime: number;
  unmountTime: number;
  updateTimes: number[];
  renderCount: number;
  errorCount: number;
}

export interface ComponentError {
  componentId: string;
  error: Error;
  phase: 'mount' | 'unmount' | 'update' | 'render';
  timestamp: Date;
  inputs?: Record<string, unknown> | undefined;
}

/**
 * Enhanced base component with lifecycle management, error boundaries, and performance monitoring
 */
export abstract class EnhancedBaseComponent implements IComponent {
  public readonly container: HTMLElement;
  public inputs?: Record<string, unknown> | undefined;
  
  protected state: ComponentState = {
    isMounted: false,
    isDestroyed: false,
    hasError: false,
    updateCount: 0
  };
  
  protected metrics: ComponentPerformanceMetrics = {
    mountTime: 0,
    unmountTime: 0,
    updateTimes: [],
    renderCount: 0,
    errorCount: 0
  };

  protected listeners: Array<() => void> = [];
  protected timers: Array<number> = [];
  protected observers: Array<MutationObserver | ResizeObserver | IntersectionObserver> = [];
  protected componentId: string;

  constructor(container: HTMLElement, inputs?: Record<string, unknown>) {
    this.container = container;
    this.inputs = inputs;
    this.componentId = this.constructor.name;
    
    // Add component identifier to container
    this.container.setAttribute('data-component', this.componentId);
    this.container.setAttribute('data-component-id', this.generateUniqueId());
  }

  /**
   * Mount the component with error handling and performance monitoring
   */
  async mount(): Promise<void> {
    if (this.state.isMounted || this.state.isDestroyed) {
      return;
    }

    const startTime = performance.now();
    
    try {
      // Call lifecycle hook
      await this.onBeforeMount();
      
      // Render component
      await this.render();
      
      // Call lifecycle hook
      await this.onMounted();
      
      // Update state
      this.state.isMounted = true;
      this.state.mountTime = performance.now() - startTime;
      this.metrics.mountTime = this.state.mountTime;
      this.metrics.renderCount++;
      
      // Emit mount event
      eventBus.emit('component:mounted', {
        componentId: this.componentId,
        mountTime: this.state.mountTime
      });
      
    } catch (error) {
      await this.handleError(error as Error, 'mount');
    }
  }

  /**
   * Unmount the component with cleanup
   */
  async unmount(): Promise<void> {
    if (!this.state.isMounted || this.state.isDestroyed) {
      return;
    }

    const startTime = performance.now();
    
    try {
      // Call lifecycle hook
      await this.onBeforeUnmount();
      
      // Cleanup resources
      this.cleanup();
      
      // Call lifecycle hook
      await this.onUnmounted();
      
      // Update state
      this.state.isMounted = false;
      this.state.isDestroyed = true;
      this.metrics.unmountTime = performance.now() - startTime;
      
      // Emit unmount event
      eventBus.emit('component:unmounted', {
        componentId: this.componentId,
        unmountTime: this.metrics.unmountTime
      });
      
    } catch (error) {
      await this.handleError(error as Error, 'unmount');
    }
  }

  /**
   * Update component with new inputs
   */
  async update(inputs: Record<string, unknown>): Promise<void> {
    if (!this.state.isMounted || this.state.isDestroyed) {
      return;
    }

    const startTime = performance.now();
    
    try {
      const previousInputs = this.inputs;
      this.inputs = inputs;
      
      // Check if update is needed
      if (await this.shouldUpdate(previousInputs, inputs)) {
        // Call lifecycle hook
        await this.onBeforeUpdate(previousInputs, inputs);
        
        // Re-render
        await this.render();
        
        // Call lifecycle hook
        await this.onUpdated(previousInputs, inputs);
        
        // Update metrics
        const updateTime = performance.now() - startTime;
        this.state.updateCount++;
        this.metrics.updateTimes.push(updateTime);
        this.metrics.renderCount++;
        
        // Emit update event
        eventBus.emit('component:updated', {
          componentId: this.componentId,
          updateTime,
          updateCount: this.state.updateCount
        });
      }
      
    } catch (error) {
      await this.handleError(error as Error, 'update');
    }
  }

  /**
   * Handle component errors with recovery
   */
  protected async handleError(error: Error, phase: 'mount' | 'unmount' | 'update' | 'render'): Promise<void> {
    this.state.hasError = true;
    this.state.errorMessage = error.message;
    this.metrics.errorCount++;
    
    const componentError: ComponentError = {
      componentId: this.componentId,
      error,
      phase,
      timestamp: new Date(),
      inputs: this.inputs
    };
    
    // Emit error event
    eventBus.emit('component:error', componentError);
    
    // Try to render error state
    try {
      await this.renderError(error);
    } catch (renderError) {
      // Fallback to basic error display
      this.container.innerHTML = `
        <div class="component-error">
          <h3>Component Error</h3>
          <p>Component ${this.componentId} encountered an error</p>
          <details>
            <summary>Error Details</summary>
            <pre>${error.message}</pre>
          </details>
        </div>
      `;
    }
    
    // Call error recovery hook
    await this.onError(error, phase);
  }

  /**
   * Cleanup resources
   */
  protected cleanup(): void {
    // Remove event listeners
    this.listeners.forEach(remove => remove());
    this.listeners.length = 0;
    
    // Clear timers
    this.timers.forEach(timer => clearTimeout(timer));
    this.timers.length = 0;
    
    // Disconnect observers
    this.observers.forEach(observer => observer.disconnect());
    this.observers.length = 0;
    
    // Clear container
    this.container.innerHTML = '';
  }

  /**
   * Add event listener with automatic cleanup
   */
  protected addEventListener<K extends keyof HTMLElementEventMap>(
    element: HTMLElement,
    type: K,
    listener: (this: HTMLElement, ev: HTMLElementEventMap[K]) => void,
    options?: boolean | AddEventListenerOptions
  ): void {
    element.addEventListener(type, listener, options);
    this.listeners.push(() => element.removeEventListener(type, listener, options));
  }

  /**
   * Add timer with automatic cleanup
   */
  protected setTimeout(callback: () => void, delay: number): number {
    const timer = window.setTimeout(callback, delay);
    this.timers.push(timer);
    return timer;
  }

  /**
   * Add observer with automatic cleanup
   */
  protected addObserver(observer: MutationObserver | ResizeObserver | IntersectionObserver): void {
    this.observers.push(observer);
  }

  /**
   * Generate unique component instance ID
   */
  protected generateUniqueId(): string {
    return `${this.componentId}-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
  }

  /**
   * Get component performance metrics
   */
  public getMetrics(): ComponentPerformanceMetrics {
    return { ...this.metrics };
  }

  /**
   * Get component state
   */
  public getState(): ComponentState {
    return { ...this.state };
  }

  // Lifecycle hooks (to be implemented by subclasses)
  protected async onBeforeMount(): Promise<void> {}
  protected async onMounted(): Promise<void> {}
  protected async onBeforeUpdate(previousInputs?: Record<string, unknown>, newInputs?: Record<string, unknown>): Promise<void> {}
  protected async onUpdated(previousInputs?: Record<string, unknown>, newInputs?: Record<string, unknown>): Promise<void> {}
  protected async onBeforeUnmount(): Promise<void> {}
  protected async onUnmounted(): Promise<void> {}
  protected async onError(error: Error, phase: string): Promise<void> {}

  // Abstract methods (to be implemented by subclasses)
  protected abstract render(): Promise<void>;
  protected async renderError(error: Error): Promise<void> {
    // Default error rendering - can be overridden
    this.container.innerHTML = `
      <div class="component-error">
        <p>Error in ${this.componentId}: ${error.message}</p>
      </div>
    `;
  }
  
  protected async shouldUpdate(previousInputs?: Record<string, unknown>, newInputs?: Record<string, unknown>): Promise<boolean> {
    // Default implementation - shallow comparison
    if (!previousInputs && !newInputs) return false;
    if (!previousInputs || !newInputs) return true;
    
    const prevKeys = Object.keys(previousInputs);
    const newKeys = Object.keys(newInputs);
    
    if (prevKeys.length !== newKeys.length) return true;
    
    return prevKeys.some(key => previousInputs[key] !== newInputs[key]);
  }
}