/**
 * Enhanced Base Component with Styling Support
 * Extends the base component with integrated styling capabilities
 */

import { EnhancedBaseComponent } from '../component-system/enhanced-base-component.js';
import { styleManager, type ComponentStyles, type StylesOptions } from '../styling/component-style-manager.js';

/**
 * Styled Component Base Class
 * Provides integrated styling capabilities with scoped CSS
 */
export abstract class StyledComponent extends EnhancedBaseComponent {
  protected scopeId?: string;
  protected styleOptions: StylesOptions;

  constructor(container: HTMLElement, inputs?: Record<string, unknown>, styleOptions: StylesOptions = {}) {
    super(container, inputs);
    this.styleOptions = { scoped: true, ...styleOptions };
  }

  /**
   * Register component styles
   */
  protected registerStyles(styles: ComponentStyles): string {
    this.scopeId = styleManager.registerComponentStyles(
      this.componentId,
      styles,
      this.styleOptions
    );
    
    // Add scope attribute to container
    this.container.setAttribute('data-component-scope', this.scopeId);
    
    return this.scopeId;
  }

  /**
   * Create scoped class name
   */
  protected className(name: string): string {
    if (!this.scopeId) {
      console.warn(`Component ${this.componentId} has no scope ID. Did you forget to call registerStyles()?`);
      return name;
    }
    return styleManager.getScopedClassName(name, this.scopeId);
  }

  /**
   * Apply theme variables
   */
  protected applyTheme(themeVariables: Record<string, string>): void {
    styleManager.applyTheme(this.componentId, themeVariables);
  }

  /**
   * Hot reload styles (for development)
   */
  protected hotReloadStyles(newStyles: ComponentStyles): void {
    if (this.scopeId) {
      styleManager.hotReloadStyles(this.componentId, newStyles);
    }
  }

  /**
   * Cleanup - remove styles when component is destroyed
   */
  protected override cleanup(): void {
    super.cleanup();
    
    // Remove component styles
    styleManager.removeComponentStyles(this.componentId);
  }

  /**
   * Get styles for this component (to be implemented by subclasses)
   */
  protected abstract getStyles(): ComponentStyles;

  /**
   * Initialize styles before mounting
   */
  protected override async onBeforeMount(): Promise<void> {
    // Register styles before mounting
    const styles = this.getStyles();
    this.registerStyles(styles);
    
    await super.onBeforeMount();
  }
}

/**
 * Utility function to create styled component styles
 */
export function createStyles(
  styles: Omit<ComponentStyles, 'componentId'>,
  componentId?: string
): ComponentStyles {
  return {
    componentId: componentId || 'unknown',
    ...styles
  };
}

/**
 * CSS template literal with TypeScript support
 */
export function css(template: TemplateStringsArray, ...values: (string | number)[]): string {
  return styleManager.css(template, ...values);
}

